import echoneSku from '@/components/echone-sku/echone-sku'
import instance from "../../api/index.js"
export default {
	data() {
		return {
			themeColor: '#1ac792',
			popupShow: false,
			path:'',
			info:'',
			list:[],
			shopNo:"",
			carinfo:'',
			btnnum:0,
			carlength:'',
			combinationsProps: {
				id: 'productNo',
				value: 'skuValue',
				image: 'skuImage',
				price: 'skuPrice'
			},
			specificationsProps: {
				id: 'id',
				list: 'skuList',
				name: 'skuName'
			},
			combinations: [
				
			],
			specifications: [
				
			],
			selectedIndex: 0,
		}
	},
	onLoad(options) {
		this.token=uni.getStorageSync("token");
		this.productNo=options.productNo
		instance({
			url:"/api/product/queryProductDetail",
			data:{
				productNo:this.productNo
				},
				method:'POST',
				header:{
				   'content-type':'application/json',
			}
		}).then(res=>{
			console.log(res)
			this.shopNo=res.data.data.shopNo
			this.list=res.data.data
			let path=this.list.productBanner.split(",")
			this.path=path
		})
		instance({
			url:"/api/product/queryProductSetByProductNo",
			data:{
				token:this.token,
				productNo:this.productNo
				},
				method:'POST',
				header:{
				   'content-type':'application/json',
			}
		}).then(res=>{
			// console.log(res)
			let info1={"skuName":'款式',id:123}
			let info2={"skuName":'规格',id:456}
			
			let colorarr=[]
			let name=[]
			name.push(res.data.data[0].color)
			let all=[]
			res.data.data[0].sizeList.forEach(item=>{
				colorarr.push(item.size)
				let info={"skuValue":name+","+ item.size,"skuImage":"http://cjxx.zhjfd.com/upload"+item.imgPath,"skuPrice":item.agentPrice,"productSetNo":item.productSetNo,"productNo":item.productNo,"shopNo":this.shopNo}
				all.push(info)
			})
			info1.skuList=name
			info2.skuList=colorarr
			// console.log(info2)
			this.specifications.push(info1,info2)
			this.combinations=all
		})
		instance({
			url:"/api/shopCar/queryMyShopCarNum",
			data:{
				token:this.token
			},
			method:"POST",
			header:{
				'content-type':'application/json',
			}
		}).then(res=>{
		this.carlength=res.data.data
		})
	},
	methods: {
		change(e) {
			this.btnnum = e
			console.log(this.btnnum)
		},
		getcarlength(){
			instance({
				url:"/api/shopCar/queryMyShopCarNum",
				data:{
					token:this.token
				},
				method:"POST",
				header:{
					'content-type':'application/json',
				}
			}).then(res=>{
			this.carlength=res.data.data
			})
		},
		getcarlist(){
			instance({
				url:"/api/shopCar/queryMyShopCarInfo",
				data:{
					token:this.token
					},
					method:'POST',
					header:{
					   'content-type':'application/json',
				}
			}).then(res=>{
				// console.log(res)
				this.cartlist=res.data.data[0].productList
				this.cartlist.forEach(item=>{
					item.flag=true
				})
				uni.setStorageSync("cartlist",res.data.data[0].productList)
			})
		},
		joincar(){
			instance({
				url:"/api/shopCar/addShopCar",
				data:{
					token:this.token,
					productNo:this.carinfo.productNo,
					num:this.carinfo.num,
					shopNo:this.carinfo.shopNo,
					productSetNo:this.carinfo.productSetNo
					},
					method:'POST',
					header:{
					   'content-type':'application/json',
				}
			}).then(res=>{
				uni.showToast({
					icon: 'none',
					title: '加入购物车成功'
				})
				this.getcarlength()
				console.log(res)
			})
			
		},
		
		selectSku() {
			this.popupShow = true
		},
		//异步请求示例
		initData() {
		
		},
		handleClose() {
			this.popupShow = false
		},
		handleConfirm(obj) {
			console.log(obj)
			this.carinfo=obj
			this.info = obj.skuValue
			this.popupShow = false
		},
		toindex(){
			uni.switchTab({
				url:"../index/index"
			})
		},
		tocar(){
			uni.switchTab({
				url:"../car/car"
			})
		}
	}
}
